.App {
  text-align: center;
}

.home-content {
  margin: 0;
}

html,
body,
#root {
    width: 100%;
    height: 100%;
    background-color: inherit;
    text-align: center;
}

/******* Nav Styling *******/
.nav {
  display: block;
  height: 2rem;
  margin: 1rem 0 0 5%;
  padding: 0;
  list-style: none;
  justify-content: space-around;
}

.links, .links h3 {
  font-weight: 500;
  font-size: 1.125rem;
}

.links {
  display: inline;
  float: left;
  font-family: sans-serif;
  color: #999;
  text-decoration: none;
  margin: 0 2.5rem 0 0;
  padding: 0;
}

h3.links {
  color: #07E0E0;
  text-decoration: underline;
}

/******* Universals Styling *******/

/*  Universal  */

.header {
  height: 4.875rem;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  background-color: #282E31;
}

/*  Universal  */
.header img {
  margin-left: 5%;
}

.header img:nth-of-type(2) {
  margin: 0 1.5rem 0 auto;

}

.header h3 {
  color: #fff;
  text-decoration: none;
  margin: 0 0 0 3rem;
}

/*  Universal  */
.info {
  width: 90%;
  margin: 0.5rem auto;
  display: block;
  text-align: left;
}

/*  Universal  */
.info h3 {
  display: inline;
  margin: 0;
  margin-left: 0;
}

.title h3 {
  font-weight: 300;
  font-size: 1.5rem;
}

/*  Universal  */
.back {
  color: #07E0E0;
  text-decoration: none;
}

.back h3 {
  font-weight: 400;
  font-size: 1.25rem;
}

.info.back {
  margin: 0.5rem 0 0 3rem;
}

.info .back {
  margin: 0.5rem 0 0 0;
}

/*  Universal  */
.title {
  font-weight: 300;
  font-size: 1.5rem;
  margin-bottom: 0;
}

/*  Universal  */
.add-container h3 {
  display: inline;
  font-weight: 500;
  font-size: 3rem;
}

.add-container h5 {
  transform: translate(0, -4rem);
  margin-bottom: -2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
}

.add-container h5 span {
  font-size: 0.8rem;
  font-weight: 500;
  display: flex;
  flex-direction: row;
  padding: 0;
  margin: 0;
  align-self: center;
  color: #1890ff;
}

/*  Universal  */
.add-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: baseline;
}

/*  Universal  */
.add-container button {
  display: inline;
  float: right;
}

.add-container span {
  padding: 0 0.5rem 0 0;
}

/*  Universal  */
.add-container .add-input {
  display: flex;
  flex-direction: row;
  align-items: center;
  background: 0;
  margin: 0;
  padding: 0;
  font-size: 1rem;
  font-weight: 800;
  color: #07E0E0;
}

.add-container .add-input span {
  transform: translate(0,0.2rem);
}

.add-container-client {
  margin-top: 2.5rem;
}

/*  Universal  */
.storage-container {
  width: 100%;
  margin: 1.5rem auto;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
}

/*  Universal  */
.labels {
  margin: 0;
  padding: 0;
  display: block;
  width: 90vw;
}

/*  Universal  */
.labels h5 {
  padding: 0;
  margin: 0;
  display: inline;
  color: #333;
  font-weight: 100;
  font-size: 1.2em;
}

/*  Universal  */
.labels h5:first-child {
  margin-left: 3rem;
  float: left;
}

/*  Universal  */
.card, .card-open {
  display: block;
  height: 6.25rem;
  width: 90vw;
  margin: 0.5rem auto;
  border-radius: 0.625rem;
  background-color: #fff;
  color: #07E0E0;
  font-size: 1.2em;
  letter-spacing: 0.125rem;
  box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.05);
}

/*  Universal  */
  .card .content, .card-stats {
    height: 6.25rem;
    width: 90vw;
    display: table-cell;
    vertical-align: middle;
  }

  .card-stats h3 {
    text-decoration: 0;
  }

/*  Universal  */
  .card-name {
    color: #07E0E0;
    line-height: 3.5em;
    float: left;
    margin-left: 3rem;
  }

  .card-open {
    margin-bottom: 0;
    border-radius: 0.625rem 0.625rem 0 0;
    box-shadow: 0;
    border-bottom: 0.1rem solid #000;
  }



/*  Universal  */
  button,
  .add-input[type=submit] {
    outline: none;
    border: none;
    transition: background-color 0.2s ease,
                color 0.2s ease;
  }

  button,
  .add-input[type=submit]:hover {
    color: #07C0D0;
    cursor: pointer;
  }


/* modals */
