.export-button {
  float: right;
  margin-right: 0;
  color:white;
  background-color: #07E0E0;
  padding: 0.625rem;
  border-radius: 0.625rem;
  font-size: 1rem;
  text-decoration: none;
}

.edit-icon-button {
  background-color: white;
  margin-top: -1.5rem;
  margin-bottom: 1rem;
  width: 1.5rem;
  margin-left: 7.5rem;
}

.card-leaderboard {
  display: block;
  width: 90vw;
  margin: 0 auto 0.625rem;
  border-radius: 0 0 0.625rem 0.625rem;
  background-color: #fff;
  background: #fff;
  box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034),
              0 6.7px 5.3px rgba(0, 0, 0, 0.048),
              0 12.5px 10px rgba(0, 0, 0, 0.06),
              0 22.3px 17.9px rgba(0, 0, 0, 0.072),
              0 41.8px 33.4px rgba(0, 0, 0, 0.086),
              0 100px 80px rgba(0, 0, 0, 0.12);
}

.card-leaderboard-labels {
  width: 90vw;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.card-leaderboard-labels h3 {
  margin: 0;
  padding: 0;
  font-size: 1rem;
  font-weight: 400;
  color: #888888;
  line-height: 2rem;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}

.card-leaderboard-labels span {
  margin: 0.2rem 0 0 0.2rem;
  cursor: pointer;
}

.card-leaderboard-labels .label-icon {
  margin: 0 0 0 0.5rem;
}

.card-leaderboard-users, .card-stats {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.card-leaderboard-users:hover {
  background: rgba(231, 229, 229, 0.46);
  box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.05);
}

.card-leaderboard-users h3, .card-stats h3 {
  color: #282E31;
}

.card-leaderboard-users h3, .card-leaderboard-labels h3, .card-stats h3, .card-stats .card-stats-title h3 {
  font-weight: 300;
  font-size: 1.5rem;
  text-align: left;
  flex: 2;
}

.card-leaderboard-labels h3 {
  font-weight: 800;
  font-size: 0.875rem;
}

.card-leaderboard-users h3:first-child, .card-leaderboard-labels h3:first-child, .card-stats h3:first-child {
  margin-left: 2.5rem;
  text-align: left;
  flex: 3;
}

.card-stats h3:nth-child(2) {
  margin-left: -1.25rem;
}

.leaderboard-header {
  display: block;
  height: 5rem;
  background: #fff;
  display: flex;
  align-items: center;
}

.leaderboard-header span {
  float: left;
  padding: 1rem 1rem;
  margin-left: 1.5rem;
  font-weight: 800;
  font-size: 1.125rem;
  color: #fff;
}

.caret {
  margin: 0 0.5rem 0 0;
  transform: translate(0, 0.1rem);
}

.card-stats-title {
  height: 60%;
  margin-left: 2.5rem;
  text-align: left;
  flex: 3;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.card-stats-title h3 {
  transform: translate(-3rem);
  display: flex;
  flex-direction: row;
  align-items: baseline;
}

.card-stats-title h3.complete-title {
  transform: translate(-4.25rem);
  display: flex;
  flex-direction: row;
  align-items: baseline;
}

.card-stats-title .complete {
  transform: translate(0, -0.7rem);
  color: #888;
  font-weight: 300;
  font-size: 1.125rem;
}

.card-stats-title .sub-stat {
  margin: 0;
  padding: 0;
  transform: translate(-0.4rem, -0.7rem);
  color: #888;
  font-weight: 300;
  font-size: 1.125rem;
}

.card-leaderboard-labels h3:first-child {
  margin-left: 0;
  margin-right: 1.25rem;
}

.card-stats h3:first-child {
  color: #07E0E0;
}

.team-active {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.team-active span {
  color: #1890ff;
  font-size: 0.8rem;
  font-weight: 500;
}

.quizzes-editGame{
  font-size: 1.5rem;
}

.game-name-editGame {
  font-size: 3rem;
}

.time-limit-editGame {
  font-weight: bold !important;
  font-size: 0.875rem !important;
  display: block !important;
}

.time-select {
  float: left;
  margin-bottom: 2rem;
}

.title-quiz {
    margin-left: 2rem;
}

h3.inactive {
  color: #999;
}
