body {
  background: #f3f3f3;
}

h2 {
  color: #A15BFA;
  margin: 0;
  padding: 0 0 0.625rem;
}

.game-and-score {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  align-items: center;
  width: 90vw;
  max-width: 50rem;
  margin: 1.25rem auto;
  padding: 0;
  text-align: left;
  font-size: 2rem;
}

.finish-game-name {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  align-items: center;
  width: 90vw;
  margin: 1.25rem auto -4rem;
  padding: 0;
  text-align: left;
  font-size: 2rem;
}

.game-and-score h3, .game-and-score h5 {
  font-weight: 500;
  margin: 0;
  padding: 0.25rem 0;
}

.game-and-score h5 {
  color: #A15BFA;
}

.progress {
  width: 90vw;
  max-width: 50rem;
  margin: auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 2rem;
}

.bar {
  width: 30rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.bar h5, .time h5 {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  margin: 0;
  padding: 0;
  line-height: 2rem;
  font-size: 1rem;
  font-weight: 500;
  color: #A15BFA;
}

.time h5 {
  justify-content: space-between;
  width: 5.5rem;
}

.quiz-area {
  min-height: 30rem;
  width: 90vw;
  max-width: 50rem;
  margin: 1.25rem auto;
  background: #FFFFFF;
  box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.05);
  border-radius: 0.625rem;
}

.question {
  margin: 0;
  padding: 0.625rem 0 0;
  font-style: normal;
  font-weight: bold;
  font-size: 2rem;
  border-radius: 0.625rem;
  color: #282E31;
}

.question:hover {
  border: none;
}

.question h3 {
  margin: 0;
  padding: 0 0 0.3rem;
}

.question h6 {
  margin: 0;
  padding: 0;
  font-weight: 100;
  font-size: 1rem;
}

.questions {
  margin: 6.25rem auto 0;
}

.questions button {
  border-radius: 0.625rem;
  padding: 1rem 2.5rem;
  background: #07E0E0;
  color: #fff;
  font-size: 1.4rem;
}

.answers {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
}

.answer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 20vh;
  width: 30vw;
  max-width: 20rem;
  margin: 1.25rem 2rem 1.25rem;
  padding: 0;
  background: #FFFFFF;
  mix-blend-mode: normal;
  box-shadow: 0px 32px 40px rgba(210, 210, 210, 0.54);
  border-radius: 0.625rem;
}

.answer:hover {
  box-shadow: 0px 32px 80px rgba(150,150,150, 0.55);
  border: none;
}

.answer h4 {
  margin: 2rem 1rem;
  padding: 0;
  font-size: 1.825rem;
  font-weight: 800;
  color: #000;
}

.correct, .correct:hover {
  border: 0.1rem solid #1DC44B;
  box-shadow: 0px 32px 40px rgba(29, 195, 75, 0.12);
}

.correct h4 {
  color: #1DC44B;
  background: 0;
}

.wrong, .wrong:hover {
  border: 0.1rem solid #f00;
}

.wrong h4 {
  color: #f00;
  background: 0;
}

.times-up-background {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background: rgba(0, 0, 0, 0.33);
}

.times-up-modal {
  position: fixed;
  top: 50vh;
  left: 50vw;
  height: 20rem;
  width: 25rem;
  transform: translate(-50%, -40%);
  background: #FFFFFF;
  box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.05);
  border-radius: 0.625rem;
}

.times-up-modal-content {
  margin: 2rem 0 0;
}

.times-up-modal span {
  font-size: 2rem;
  margin: 0;
  padding: 0;
}

.times-up-modal h2 {
  margin: 0.625rem 0;
  padding: 0;
  font-size: 3rem;
  color: #f00;
}

.times-up-modal h4 {
  text-align: center;
  margin: 2rem 0 0;
  padding: 0;
  font-size: 1rem;
  color: #282E31;
}

.times-up-modal h3 {
  margin: 0;
  padding: 0;
  font-size: 4rem;
  font-weight: 300;
  color: #A15BFA;
}
